import { mapActions } from 'vuex'
import MyHeader from '../../components/header/header'
import AddAddress from '../../components/addAddress/addAddress'
import { getMyAddress, setAddress } from '../../api/http'

export default {
	data () {
		return {
			addressList: [],
			addressModelState: false,
			item:{}
		}
	},
  methods: {
  	/*
  	* 获取地址
  	*/
  	_getMyAddress () {
  		getMyAddress().then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.addressList = res.data
  			}else if(res.status === 'error'){
  				console.log(res.data)
  			}else if(res.error === 'token failed'){
  				console.log(res.error)
  			}
  		})
  	},
  	/*
  	* 设置默认／删除地址
  	*/
  	_setAddress (id,type) {
  		setAddress(id,type).then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this._getMyAddress()
  				this.selectPrompt({
  					promptData:{ // 数据
              content: res.data,
              type:'success'
            },
            promptState:true, // 状态
  				})
  			}else if(res.status === 'error'){
  				this.selectPrompt({
  					promptData:{ // 数据
              content: res.data,
              type:'error'
            },
            promptState:true, // 状态
  				})
  			}else if(res.error === 'token failed'){
  				console.log(res.error)
  			}
  		})
  	},
  	/*
  	* 新增地址mdel
  	* state=true(打开)
  	* state=false(关闭)
  	*/
  	modelToogle (state,i) {
  		this.addressModelState = state
  		this.item = i || undefined
  		console.log(this.item)
  	},
  	...mapActions([
      'selectPrompt'
    ])
  },
  created () {
  	this._getMyAddress()
  },
  components: {
  	AddAddress,
  	MyHeader
  }
}



