import { getGoodsDetailsFun, getShopDetailsFun, collectionFun } from '../../../api/http'
import MyBanner from '../../../components/banner/banner'
import GShop from './private/shop' 
import GoodsSpec from './private/goodsSpec'
import { mapActions } from 'vuex'

export default {
	data() {
		return {
			goodsDetails: {}, // 商品详情
			shopDetails: {},  // 店铺信息
			specState: false,  // 规格状态
			specType: null,   // 规格类型，'car'加入购物车，'pay'立即购买
		}
	},
  methods: {
  	// 获取商品详情
  	_getGoodsDetailsFun() {
  		getGoodsDetailsFun(this.$route.query.goods_id)
  		.then((res) => {
        console.log(res)
        if(res.status ==='ok'){
          this.goodsDetails = res.data
          this._getShopDetailsFun(res.data.merchants_id)
        }else if(res.status === 'error'){
          console.log(res.data)
        }
      })
  	},

  	// 获取店铺详情
  	_getShopDetailsFun(id) {
  		getShopDetailsFun(id)
  		.then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
          this.shopDetails = res.data
        }else if(res.status === 'error'){
          console.log(res.data)
        }
  		})
  	},
  	// 规格model事件
  	specModelToggle(state,type) {
  		if(state == 0){ // 关闭
  			this.specType = null
  			this.specState = false
  		}else {  // 打开
  			this.specType = type
  			this.specState = true
  		}
  	},
  	
  	//收藏商品
  	collectionClick () {
  		collectionFun(this.$route.query.goods_id)
  		.then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.goodsDetails.is_collect = res.data
  				if(res.data == 1) {
  					this.selectPrompt({
	            promptData:{ // 数据
	              content: '恭喜您，收藏成功，你可在个人中心我的收藏中查看',
	              type:'success',
	            },
	            promptState:true, // 状态
	          })
  				}else{
  					this.selectPrompt({
	            promptData:{ // 数据
	              content: '取消收藏成功',
	              type:'success',
	            },
	            promptState:true, // 状态
	          })
  				}
  			}else if(res.status === 'error'){
  				console.log(res.data)
  				this.selectPrompt({
	            promptData:{ // 数据
	              content: res.data,
	              type:'error',
	            },
	            promptState:true, // 状态
	          })
  			}
  		})
  	},
  	
  	...mapActions([
  		'selectPrompt'
  	])
  },
  created () {
  	this._getGoodsDetailsFun()
  },
  components: {
  	MyBanner,
  	GShop,
  	GoodsSpec
  }
}



