import { mapActions } from 'vuex'
import MyHeader from '../../../components/header/header'
import ConfirmAddress from './private/confirm_address'
import GoodsItem from '../../../components/goodsItem/goodsItem'
import { getGoodsInfo, preOrderFun , payOrderFun } from '../../../api/http'
import storage from '../../../common/storage'

export default {
	data () {
		return {
			data: {},
			payState: true, // 支付状态
			orderJson: {
				member_id: storage.s_getItem("uid"), // 用户id
		    address_id: "", // 地址ID
		    deduct_integral_value: "", // 抵扣积分
		    coupon_ids: "", // 优惠券id,有多个用‘,’分隔
		    orderBeans:[]
			}
		}
	},
	methods: {

		//支付成功／失败／取消的跳转
		payRouter () {
			this.$router.replace({
        path:'/me'
      })
		},
		//获取地址ID
		getAddressId (id) {
			this.orderJson.address_id = id
		},

		// 获取下单商品信息
		_getGoodsInfo (param,type) {
			getGoodsInfo(param,type).then((res) => {
				console.log(res)
				if(res.status === 'ok'){
					this.data = res.data
					/*循环数据为orderJson赋值*/
		      for(let i = 0; i < res.data.list.length; i++){
		        let merchantsObj = {
		          merchants_id: res.data.list[i].merchants_id, // 商家ID
		          order_type: "", // 订单类型
		          order_remark: "", // 订单备注
		          member_group_id: "", // 团购下单 需传值 用户开团主键
		          orderGoodsBeans: []
		        };
		        this.orderJson.orderBeans.push(merchantsObj);
		        for(let j = 0; j < res.data.list[i].goods.length; j++){
		        	let goodsItem = res.data.list[i].goods
		          let goodsObj = {
		            goods_id: goodsItem[j].goods_id, // 商品ID
		            specification_id: goodsItem[j].specification_id, // 规格ID
		            goods_num: goodsItem[j].goods_num, //购买数量
		            goods_group_id: ""  // 团购下单ID
		          }
		          this.orderJson.orderBeans[i].orderGoodsBeans.push(goodsObj);
		        }
		      }
		      console.log(this.orderJson)
				}else if(res.status === 'error'){
					console.log(res.data)
				}else if(res.error === 'token failed'){
					console.log(res.error)
        }
			})
		},

		//下单
		_preOrderFun () {
			this.payState = false
			preOrderFun (this.orderJson,this.$route.query.car_ids).then((res) => {
				console.log(res.data)
				if(res.status ==='ok'){
        	this._payOrderFun(res.data.order_no)
        }else if(res.status === 'error'){
        	this.payState = true
          console.log(res.error)
        }else if(res.error === 'token failed'){

        }
			})
		},

		//支付
		_payOrderFun (order_no) {
			payOrderFun (order_no,1).then((res) => {
				if(res.status ==='ok'){
        	pingpp.createPayment(res.data, function (result, error) {
	          if (result == "success") { // 支付成功
	          	this.selectPrompt({
		            promptData:{ // 数据
		              content:'支付成功',
		              type:'success'
		            },
		            promptState:true, // 状态
		          })
		          this.payRouter()
	          } else if (result == "fail") { // 支付失败
	          	this.selectPrompt({
		            promptData:{ // 数据
		              content:'支付失败',
		              type:'error'
		            },
		            promptState:true, // 状态
		          })
	            this.payRouter()
	          } else if (result == "cancel") { // 支付取消
	          	this.selectPrompt({
		            promptData:{ // 数据
		              content:'支付取消',
		              type:'error'
		            },
		            promptState:true, // 状态
		          })
	            this.payRouter()
	          }
	        })
        }else if(res.status === 'error'){
        	this.payState = true
          console.log(res.error)
        }else if(res.error === 'token failed'){

        }
			})
			.catch((e) => {
				this.payState = true
				console.log(e)
			})
		},
		...mapActions([
      'selectPrompt'
    ])
	},
	created () {
		if(this.$route.query.car_ids){
			this._getGoodsInfo(this.$route.query.car_ids,1)
		}else{
			this._getGoodsInfo(storage.s_getItem('confirmGoodsObj'),0)
		}
	},
  components: {
  	MyHeader,
  	ConfirmAddress,
  	GoodsItem
  }
}



