import { getOrderDetailsFun } from '../../../api/http'
import MyHeader from '../../../components/header/header'
import OrderState from './private/orderState'
import OrderOperate from '../orderOperate/orderOperate'

export default {
	data () {
		return {
			orderInfo: {}
		}
	},
  methods: {
  	// 获取订单详情
  	_getOrderDetailsFun () {
  		getOrderDetailsFun(this.$route.query.order_id)
  		.then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.orderInfo = res.data
  			}else if(res.status === 'error'){
  				console.log(res.data)
  			}else if(res.error === 'token failed'){
					console.log(res.error)
				}
  		})
  	}
  },
  created () {
  	this._getOrderDetailsFun()
  },
  components: {
  	MyHeader,
  	OrderState,
  	OrderOperate
  }
}



