import MyHeader from '../../components/header/header'
import { getUserInfoFun, upImg, editUserInfo } from '../../api/http'
import storage from '../../common/storage'
import { mapActions } from 'vuex'

export default {
	data () {
		return {
			userInfo: {}
		}
	},
  methods: {
  	/*
  	* 获取用户信息
  	*/
  	_getUserInfoFun () {
  		getUserInfoFun().then((res) => {
  			console.log(res.data)
  			if(res.status === 'ok'){
  				this.userInfo = res.data
  				storage.s_setItem('userInfo',res.data)
  			}else if(res.status === 'error'){
  				console.log(res.error)
  			}
  		})
  	},
  	/*
  	* 上传图片
  	* 单张
  	*/
  	_upImg (e) {
  		upImg(e).then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.userInfo.header_img = res.data[0]
  			}
  		})
  	},
  	/*
  	* 修改个人信息
  	*/
  	_editUserInfo () {
  		let param = {
  			username: this.userInfo.username,
  			header_img: this.userInfo.header_img,
  			sex: this.userInfo.sex,
  			signature: this.userInfo.signature
  		}
  		editUserInfo(param).then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.selectPrompt({
            promptData:{ // 数据
              content: '修改成功',
              type:'success',
            },
            promptState:true, // 状态
          })
          history.back()
  			}else if(res.status === 'error'){
  				this.selectPrompt({
            promptData:{ // 数据
              content:res.data,
              type:'error',
            },
            promptState:true, // 状态
          })
  			}
  		})
  	},
  	...mapActions([
  		'selectPrompt'
  	])
  },
  created () {
  	this._getUserInfoFun();
  },
  components: {
  	MyHeader
  }
}



