import { mapGetters, mapActions } from 'vuex'

export default {
  data () {
  	return {
	  	time: null
  	}
  },
  methods: {
  	countdown () {
	    if (this.autoClose) {
	      this.time = setTimeout(() => {
	        this.close()
	      }, this.promptTime)
	    }
	  },
    // 关闭
  	close () {
  		this.selectPrompt({
        promptData:{}, // 数据
        promptState:false, // 状态
      })
  	},
    /*
    * 判断是显示还是隐藏
    */
    isShow () {
      if(this.promptState){

      }
    },
    ...mapActions([
      'selectPrompt'
    ])
  },
  watch: {
  	promptState () {
      window.clearTimeout(this.time)
	    this.countdown()
	  }
  },
  computed:{
    setStyle () {
      return {
        color: this.promptData.textColor || '#fff',
        background: this.promptData.backgroundColor ? this.promptData.backgroundColor : this.promptData.type === 'error' ? 'red' : '#21e7b6'
      }
    },
    promptTime () {
      return this.promptData.promptTime ? this.promptData.promptTime : 2000
    },
    autoClose () {
      return this.promptData.autoClose ? this.promptData.autoClose : true
    },
    ...mapGetters([
      'promptData',
      'promptState'
    ])
  },
  mounted () {
    // this.countdown()
  }
}



