import { getRecommendFun } from '../../../api/http'

export default {
  data() {
  	return {
  		recommendList:[]
  	}
  },
  methods: {
  	_getRecommendFun() {
      getRecommendFun().then((res) => {
        console.log(res)
        if(res.status ==='ok'){
          for(let i=0;i<res.data.length;i++){
            this.recommendList.push(res.data[i])
          }
        }else if(res.status === 'error'){
          console.log(res.error)
        }
      })
    }
  },
  mounted() {
  	this._getRecommendFun()
  }
}



