import { mapGetters } from 'vuex'
import { hasMsgFun , getUserInfoFun } from '../../api/http'
import storage from '../../common/storage'

import MeItem from './private/meItem'
import OrderTab from './private/orderTab'
import MyFooter from '../../components/footer/footer'

export default {
	data () {
		return {
			userInfo: {},
			hasMsg: null
		}
	},
	methods: {

  	/*
  	* 获取用户信息
  	*/
  	_getUserInfoFun () {
  		getUserInfoFun().then((res) => {
  			console.log(res.data)
  			if(res.status === 'ok'){
  				this.userInfo = res.data
  				storage.s_setItem('userInfo',res.data)
          storage.l_setItem('openid',res.data.wx_openid || '12345')
          this._hasMsgFun()
  			}else if(res.status === 'error'){
  				console.log(res.error)
  			}
  		})
  	},

  	/*
  	* 是否有未读消息
  	*/
  	_hasMsgFun () {
  		hasMsgFun().then((res) => {
        console.log(res)
        if(res.status ==='ok'){
        	this.hasMsg = res.data
        }else if(res.status === 'error'){
          console.log(res.error)
        }
      })
  	}
  },
  created () {
  	this._getUserInfoFun()
  },
  components: {
  	MyFooter,
  	MeItem,
  	OrderTab
  }
}



