import { orderOperate, payOrderFun } from '../../../api/http'
import { mapActions } from 'vuex'

export default {
	props: {
		state: {
			type: String,
			default: null
		},
		orderInfo: {
			type: Object,
			default () {
				return {}
			}
		},
		onRefresh: {
			type: Function,
			default: undefined
		}
	},
	data () {
		return {
			orderData: {}
		}
	},
  methods: {
  	/*
  	* 订单操作
  	*/
  	_orderOperate (id,type) {
  		orderOperate(id,type).then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				if(this.onRefresh){
  					if(type == 5){
	  					history.back()
	  				}else{
  						this.onRefresh()
  					}
  				}
  				this.selectPrompt({
            promptData:{ // 数据
              content:res.data,
              type:'success',
            },
            promptState:true, // 状态
          })
  			}
  		})
  	},
  	/*
  	* 继续支付
  	*/
  	_payOrderFun () {
  		payOrderFun(this.orderInfo.pay_no,2)
  		.then((res) => {
  			console.log(res)
  			if(res.status ==='ok'){
        	pingpp.createPayment(res.data, function (result, error) {
	          if (result == "success") { // 支付成功
	          	this.selectPrompt({
		            promptData:{ // 数据
		              content:'支付成功',
		              type:'success'
		            },
		            promptState:true, // 状态
		          })
		          if(this.onRefresh){
		          	this.onRefresh()
		          }
	          } else if (result == "fail") { // 支付失败
	          	this.selectPrompt({
		            promptData:{ // 数据
		              content:'支付失败',
		              type:'error'
		            },
		            promptState:true, // 状态
		          })
	          } else if (result == "cancel") { // 支付取消
	          	this.selectPrompt({
		            promptData:{ // 数据
		              content:'支付取消',
		              type:'error'
		            },
		            promptState:true, // 状态
		          })
	          }
	        })
        }else if(res.status === 'error'){
          console.log(res.data)
        }else if(res.error === 'token failed'){
        	console.log(res.error)
        }
  		})
  	},
  	...mapActions([
  		'selectPrompt'
  	])
  },
  watch: {
  	orderInfo () {
  		this.orderData = this.orderInfo
  		console.log(this.orderData)
  	}
  }
}



