import { mapActions } from 'vuex'
import { calculatedPrice , addShoppingCarFun } from '../../../../api/http'
import count from '../../../../components/count/count'
import storage from '../../../../common/storage'

export default {
	props: ['specType','goodsInfo'],
  data() {
  	return {
  		data: {}, // 存放规格数据
  		specIdArr: [], // 存放规格ID
  		specNameArr: [], // 存放规格名称
      itemIdArr: [],
  		goodsNum: 1, // 存放数量
      confirmGoodsObj: {} // 存放跳转到确认订单的商品信息
  	}
  },
  methods: {
  	// 关闭model
  	closeModel(){
      this.$emit('on-close')
  	},

  	// 规格Item点击事件
  	keepSpecIdClick(paIndex,id,name,){ // paIndex:外层循环下标 , id:规格ID, name:规格名称 
      this.$set(this.specIdArr, paIndex, id)
      this.$set(this.specNameArr, paIndex, name)
      this.itemIdArr = []
      for(let i=0; i<this.specIdArr.length; i++){
        if(this.specIdArr[i]){
          this.itemIdArr.push(this.specIdArr[i])
        }
      }
      if(this.itemIdArr.length == this.specData.length){
  		  this._calculatedPrice(this.specIdArr)
      }
  	},
  	
  	// 计算价格
  	_calculatedPrice(spec_id) {
  		calculatedPrice(this.goodsInfo.goods_id,spec_id)
      .then((res) => {
  			console.log(res)
  			if(res.status ==='ok'){
          this.data = res.data
        }else if(res.status === 'error'){
          console.log(res.data)
        }
  		})
  	},

  	// 加
  	numPlus(count) {
  		this.goodsNum = count
      console.log(this.goodsNum)
  	},

  	// 减
  	numMinus(count) {
  		this.goodsNum = count
      console.log(this.goodsNum)
  	},

    // 加入购物车
    _addShoppingCarFun() {
      addShoppingCarFun(this.goodsInfo.goods_id,this.goodsNum,this.data.specification_id)
      .then((res) => {
        if(res.status ==='ok'){
          console.log(res.data)
          this.closeModel() // 关闭model
          this.selectPrompt({
            promptData:{ // 数据
              content:res.data,
              type:'success',
            },
            promptState:true, // 状态
          })
        }else if(res.status === 'error'){
          console.log(res.data)
          this.selectPrompt({
            promptData:{ // 数据
              content:res.data,
              type:'error'
            },
            promptState:true, // 状态
          })
        }
      })
    },
  	
  	// 确定
  	determineFun() {
  		console.log("确定")
      if(this.specType === 'car'){
        this._addShoppingCarFun()
      }else{
        console.log('立即购买')
        this.confirmGoodsObj = {
          goods_id: this.goodsInfo.goods_id,
          merchants_id: this.goodsInfo.merchants_id, // 商家ID
          specification_id: this.data.specification_id,
          num: this.goodsNum
        }
        console.log(this.confirmGoodsObj)
        storage.s_setItem('confirmGoodsObj',this.confirmGoodsObj)
        this.$router.push({
          path:'/confirmOrder'
        })
      }
  	},
    ...mapActions([
      'selectPrompt'
    ])
  },
  computed:{
    specData () {
      return this.goodsInfo.goodsSpecificationBeans
    }
  },
  mounted() {

  },
  components: {
  	count
  }
}



