import { delInvalidShopCar } from '../../../api/http'
import { mapActions } from 'vuex'

export default {
	props:{
    invalidInfo: {
      type: Array,
      default: function () {//默认值
        return []
    	}
    },
  },
  methods: {
  	delInvalidShopCarClick () {
  		delInvalidShopCar().then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.selectPrompt({
	          promptData:{ // 数据
	            content:res.data,
	            type:'success'
	          },
	          promptState:true, // 状态
	        })
	        this.$emit('on-del')
  			}else if(res.status === 'error'){
  				console.log(res.data)
  				this.selectPrompt({
	          promptData:{ // 数据
	            content:res.data,
	            type:'error'
	          },
	          promptState:true, // 状态
	        })
  			}else if(res.error === 'token failed'){
  				console.log(res.error)
  			}
  		})
  	},
  	...mapActions([
      'selectPrompt'
    ])
  },
  mounted () {
  	console.log(this.invalidInfo)
  }
}



